import { BiFactory } from '@ali/4ever-cangjie';
import { key } from '../utils/meta';

export const biFactory: BiFactory = (configs) => {
  const { getColorPanelMode } = configs.pref || {};
  return {
    key,
    queries: {
      getColorPanelMode: () => {
        return typeof getColorPanelMode === 'function' ?
          getColorPanelMode() : 'normal';
      },
    },
  };
};
